import React from "react";
import "./loadingBar.css";

const LoadingBar = () => {
	return (
			<div className="loadingBar">
				<div className="bounce1"/>
				<div className="bounce2"/>
				<div className="bounce3"/>
			</div>
	);
};

export default LoadingBar;


